@import "../../variables.scss";

.cardFront {
  width: 100%;
  height: 100%;
  padding-top: 2em;
}

img {
  height: 35%;
  width: 18%;
  border-radius: 2px 2px 0 0;
  padding: 1.5em;
  border: solid 2px rgb(9, 194, 194);
  border-radius: 10px;
}

.cardTitle {
  padding-top: 0.5em;
  font-size: 1em;
  text-align: center;
  color: rgb(9, 194, 194);
  font-family: $font-family;
}

// p {
//   font-size: 1em;
//   text-align: center;
// }

.beerDetails {
  height: 40%;
  width: 100%;
  padding: $base-spacing * 4;
  border-top: none;
  border-radius: 0 2px 2px 0;
  display: flex;
  flex-direction: column;
  padding-top: $base-spacing * 2;
}

.cardfrontName {
  color: $primary-color;
  font-size: 1.3rem;
  margin-bottom: $base-spacing;
  overflow: hidden;
  text-overflow: ellipsis;
  justify-content: center;
  font-family: $font-family;
  text-align: center;

  &:hover {
    color: $primary-color;
  }
}

.beertagline {
  color: $primary-color;
  font-size: 0.9rem;
  border: $primary-border;
  width: 200px;
  text-align: center;
  font-family: $font-family;
  margin-left: 3em;
}

